.footer {
  display: flex;
  justify-content: space-between;
  width: 75vh;
  transform: rotate(-90deg) translateX(100%);
  transform-origin: right bottom;
  position: absolute;
  bottom: 5vh;
  right: 2.8vw;
  z-index: 3;
}
.footer__menu {
  display: flex;
}
.footer__item {
  padding: 0 16px;
}
.footer__link {
  font-weight: 600;
  transition: color 0.2s ease-in;
}
.footer__link:hover {
  color: var(--accent-color);
}
.footer__link:active {
  opacity: 0.8;
}
.footer__link:focus {
  color: var(--accent-color);
}